<template>
  <div id="shopcar">

    <div class="header">
      <div class="l-float">
        <em class="iconfont icon-arrow"></em>
        <a href="javascript:;" onclick="window.history.go(-1)">返回</a>
      </div>
      <div class="r-float">购物车</div>
    </div>

    <div class="nav">
      <div class="nav_check" @click="handleChecked">
        <input type="checkbox" id="check_all" v-model="allChecked" >  
        <p >全选</p>
      </div>
      <div class="del" @click="clearBuycar()" v-show="buycar.length">清空</div>
    </div>

    <div class="content">
      <!-- <button @click="clearBuycar()" v-show="buycar.length">清空</button> -->
      <div v-for="item of buycar" :key="item._id">
        <div class="check" @click="checked($event,item.price,item.num)">
          <input type="checkbox" name="check" id="check" :checked="minChecked" class="check-count">
        </div>
        <div class="img">
          <img :src="item.src" alt="">
        </div>
        <div class="introduce">
          <span>{{item.title}}</span>
          <p>介绍:{{item.produce}}</p>
          <p>价格:￥{{item.price}}.00</p>
          <div>
            <div class="quant">数量</div>
            <div class="reduce iconfont icon-jian" @click="changeItem({_id:item._id,num:-1,price:item.price})"></div>
            <input type="text" class="num" :value="item.num">
            <div class="add iconfont icon-jia" @click="changeItem({_id:item._id,num:1,price:item.price})"></div>
          </div>
        </div>
        <div class="del" @click="removeItem({_id:item._id})">
          <em class="iconfont icon-shanchu"></em>
        </div>
      </div>
    </div>

    <div class="footer">
      <div class="count">￥{{totalPrice}}</div>
      <div class="settlement">去结算({{count}})</div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Header } from 'mint-ui';
Vue.component(Header.name, Header);

import {mapGetters,mapActions} from 'vuex'

export default {
  data(){
    return {
      buycar:[],
      allChecked:false,
      minChecked:false,
      count:0,
      totalPrice:0
    }
  },
  mounted(){
    axios({
      url:'/api/shopcar',
    }).then(
      res=>{
        this.buycar = res.data.data;
        // console.log(this.buycar);
      }
    )
  },
  computed:{
  },
  methods:{
    changeItem(data){
      axios({
        url:'/api/changecar',
        method:'post',
        data:{_id:data._id,number:data.num}
      }).then(
        res => {
          // console.log(res.data);
          axios({
            url:'/api/shopcar',
          }).then(
            res=>{
              this.buycar = res.data.data;
              // console.log(this.buycar);
            }
          )
        }
      );
      let input = document.querySelector('.check-count');
      // console.log(input.checked);
      // 重新计算价格
      if(input.checked){
        if(data.num == 1){
          this.totalPrice += parseInt(data.price);
        }else{
          this.totalPrice -= parseInt(data.price);
        }
      }
    },
    
    clearBuycar(){
      axios({
        url:'/api/removecar',
      }).then(
        axios({
            url:'/api/shopcar',
          }).then(
            res=>{
              this.buycar = res.data.data;
              console.log(this.buycar);
            }
          )
      )
    },
    removeItem(_id){
      axios({
        url:'/api/removecar',
        method:'post',
        data:_id
      }).then(
        res => {
          console.log(res.data);
          axios({
            url:'/api/shopcar',
          }).then(
            res=>{
              this.buycar = res.data.data;
              console.log(this.buycar);
            }
          )
        }
      )
    },

    handleChecked(ev){
      if(this.allChecked == false){
        this.minChecked = true;
        for(var i = 0;i < this.buycar.length;i++){
          this.count = this.buycar.length;
          this.totalPrice += parseInt(this.buycar[i].num) * parseInt(this.buycar[i].price);
        }
        console.log(this.buycar[0].checked);
      }else{
        this.minChecked = false;
        for(var i = 0;i < this.buycar.length;i++){
          this.count = 0;
          this.totalPrice = 0;
        }
      }
    },

    checked(ev,price,num){
      // console.log(ev.target.checked,price,num);
      if(ev.target.checked){
        this.count += 1;
        this.totalPrice += parseInt(price) * parseInt(num);
      }else{
        this.count -= 1;
        this.totalPrice -= parseInt(price) * parseInt(num);
      }
    }
  },
  beforeRouteEnter(to,from,next){
    //异步条件
    axios({
      url:'/api/user'
    }).then(
      res=>{
        if(res.data.err===0){
          next(_this=>_this.user = res.data.data)
        }else{
          // this.$router.push('/login') ×
          // router.push('/login')
          next('/login')
        }
      }
    )
  },
}
</script>

<style>
@import url("//at.alicdn.com/t/font_1169325_cjl14h0twxd.css");
#shopcar .header{width: 100%;height: 0.8rem;text-align:center;line-height:0.8rem;background: rgb(7, 114, 25);display: flex;flex-direction: row;font-size: 0.3rem;color: #fff;}
#shopcar .header .l-float{margin: 0 2rem 0 0.1rem;}
#shopcar .header .l-float a{color: #fff;}

#shopcar .nav{height: 0.8rem;line-height: 0.8rem;display: flex;flex-direction: row;justify-content: space-between;padding: 0 0.2rem;background: #ddd;vertical-align: baseline;font-size: 0.3rem;}
#shopcar .nav .nav_check{display: flex;flex-direction: row;}
#shopcar .nav .nav_check input{margin: 0.3rem;}

#shopcar .content div{width: 100%;height: 2rem;display: flex;flex-direction: row;justify-content: space-around;}
#shopcar .content div .check{width: 10%;margin: 0.9rem 0;}
#shopcar .content div .img{width: 50%;background: #555;margin: 0.1rem;}
#shopcar .content div .img img{width: 100%;}
#shopcar .content div .introduce{display: flex;flex-direction: column;font-size: 0.3rem;}
#shopcar .content div .introduce span{margin: 0.2rem 0;}
#shopcar .content div .introduce p{font-size: 0.2rem;}
#shopcar .content div .introduce div{display: flex;flex-direction: row;justify-content: flex-start;}
#shopcar .content div .introduce div input{width: 0.5rem;height: 0.5rem;}
#shopcar .content div .introduce div div{width: 100%;height: 0.5rem;}
#shopcar .content div .introduce div .quant{width: 2rem;}
#shopcar .content div .introduce div .reduce{width: 0.5rem;font-size: 0.5rem;color: rgb(7, 114, 25);}
#shopcar .content div .introduce div .add{width: 0.5rem;font-size: 0.5rem;color: rgb(7, 114, 25);}
#shopcar .content div .introduce div .num{text-align: center;}
#shopcar .content div .del{width:10%;margin:0.2rem 0;}

#shopcar .footer {width:100%;height: 0.8rem;line-height:0.8rem;position: fixed;bottom: 0;display: flex;flex-direction: row;justify-content: space-between;border-top: 1px solid #ccc;background: #fff;}
#shopcar .footer div{text-align: center;font-size: 0.3rem;}
#shopcar .footer .count{width: 70%;}
#shopcar .footer .settlement{width: 30%;background: rgb(7, 114, 25);color: #fff;}
</style>
