<template>
  <div class="detail">
    <div class="nav">
      <div class="l-btn" @click="goback">&lt;</div>
      <div class="tab test" @click="toggleTab('good',0)">商品</div>
      <div class="tab" @click="toggleTab('part',1)">详情</div>
      <div class="tab" @click="toggleTab('appraise',2)">评价</div>
      <div class="r-btn">...</div>
    </div>
    <prince :is="currentTab" keep-alive :detail="detail"></prince>
    <div class="footer">
      <a href="">
        <em class="iconfont icon-shangpinshoucang"></em>
        <p>购物车</p>
      </a>
      <a href="">
        <em class="iconfont icon-kefu"></em>
        <p>在线客服</p>
      </a>
      <button class="l-btn" @click="addCar(detail[0])">加入购物车</button>
      <!-- <button class="r-btn" to="/compaign">立即购买</button> -->
      <router-link tag="button" class="r-btn" to="/payment" >
          <span class="home_last-active" @click="bus.$emit('send-from-detail',datail)">立即购买</span>
      </router-link>
    </div>
  </div>
</template>

<script>
// 引入子组件
import good from '../components/Good';
import part from '../components/Part';
import appraise from '../components/Appraise';

export default {
  name:"detail",
  data(){
    return {
      currentTab: 'good',
      detail:{},
    }
  },
  components:{
    good,
    part,
    appraise,
  },
  methods:{
    goback(){
      this.$router.go(-1)
    },
    toggleTab: function(tab,index) {
        // tab 为当前触发标签页的组件名
      this.currentTab = tab;
      for(var i = 0;i < document.querySelectorAll(".tab").length;i++){
          document.querySelectorAll(".tab")[i].style.borderBottom = "2px solid #fff";
      }
      document.querySelectorAll(".tab")[index].style.borderBottom = "2px solid #000";
    },
    // 添加购物车
    addCar(data){
      console.log(data);
        axios({
          url:'/api/shopcar',
          method:'post',
          data:data
        }).then((res) => {
        console.log(res.data)
        if (res.err === 0) {
          Toast('添加成功')
        }
      })
    },
  },
  created(){
    let id = this.$route.params.id;
    let dataName = this.$route.query.class;
    // 根据传送的参数，访问不同数据库，取到商品详情信息
    axios({
      url:`/api/${dataName}/${id}`,
    }).then(
      res=>{
        this.detail = res.data.data;
      }
    )
  },
  mounted(){
    bus.$emit('send-from-detail',detail)
  }
}
</script>

<style lang="scss">
@import url("//at.alicdn.com/t/font_1165586_yixc529h7ce.css");
.detail{
  .nav{width: 100%;height: 0.6rem;line-height: 0.6rem;display:flex;flex-direction: row;justify-content: space-around;margin:0.2rem 0;
    .l-btn{float: left;}
    .r-btn{float: right;}
    .test{border-bottom: 2px solid #000;}
    div{
      display: inline-block;font-size:0.3rem;
    }
  }
  .footer{width: 100%;height: 0.9rem;line-height: 1rem;display:flex;background: rgb(237, 238, 238);flex-direction: row;justify-content: space-around;position:fixed;bottom:0;
    a{display:inline-block;text-align: center;margin-top:-0.2rem;width:20%;
      em{font-size: 0.35rem;margin: 0;}
      p{font-size: 0.2rem;margin-top: -0.6rem;}
    }
    button{width:30%;font-size: 0.3rem;margin: 0;border: 0;color: #fff;}
    .l-btn{background: #000;}
    .r-btn{background: red;}
  }
}
</style>
