<template>
    <div class="follow">
        <div class="header">
            <div class="l-float">
                <em class="iconfont icon-arrow"></em>
                <a href="javascript:;" onclick="window.history.go(-1)">返回</a>
            </div>
            <div class="r-float">商品列表</div>
        </div>
        <div class="tabs">
            <div class="tab test" @click="toggleTab('care',0)">护肤</div>
            <div class="test" @click="toggleTab('makeup',1)">彩妆</div>
            <div class="test" @click="toggleTab('perfume',2)">香水</div>
            <div class="test" @click="toggleTab('tool',3)">工具</div>
            <div class="test" @click="toggleTab('man',4)">男士护肤</div>
            <div class="test" @click="toggleTab('bath',5)">洗浴护体</div>
            <div class="test" @click="toggleTab('hair',6)">美发护发</div>
            <div class="test" @click="toggleTab('gift',7)">礼品套装</div>
        </div>
         <!-- 子组件，显示不同的 tab -->
         <!-- is 特性动态绑定子组件 -->
         <!-- keep-alive 将切换出去的组件保留在内存中 -->
        <prince :is="currentTab" keep-alive :care="care" :makeup="makeup" :perfume="perfume" :tool="tool" :man="man" :bath="bath" :hair="hair" :gift="gift"></prince>
    </div>
</template>

<script>
import Vue from "vue";
import { Header } from 'mint-ui';
Vue.component(Header.name, Header);

// 引入子组件
import care from '../components/Care';
import makeup from '../components/Makeup';
import perfume from '../components/Perfume';
import tool from '../components/Tool';
import man from '../components/Man';
import bath from '../components/Bath';
import hair from '../components/Hair';
import gift from '../components/Gift';

export default {
    name:'follow',
    data(){
        return {
            currentTab: 'care',
            care:[],
            makeup:[],
            perfume:[],
            tool:[],
            man:[],
            bath:[],
            hair:[],
            gift:[],
        }
    },
    components:{
        care,
        makeup,
        perfume,
        tool,
        man,
        bath,
        hair,
        gift
    },
    // 记录页面滚动距离3.离开页面时，记录页面的滚动距离
    beforeRouteLeave(to,from,next){
        let position = document.documentElement.scrollTop;
        // console.log(position);
        localStorage.setItem('position-follow',position);
        document.documentElement.removeEventListener('scroll',this.scrollTop);
        next();
    },
    // 记录页面滚动距离4.侦听路由跳转，设置滚动高度
    watch:{
        $route(newVal){
            if(newVal.path == '/follow'){
                document.documentElement.scrollTop = localStorage.getItem('position-follow');
            }
        }
    },
    methods:{
        toggleTab: function(tab,index) {
             // tab 为当前触发标签页的组件名
            this.currentTab = tab;
            for(var i = 0;i < document.querySelectorAll(".test").length;i++){
                document.querySelectorAll(".test")[i].style.color = "#000";
            }
            document.querySelectorAll(".test")[index].style.color = "orangered";
        }
    },
    mounted(){
        axios({
            url:'/api/care',
        }).then(
        res=>{
            this.care = res.data.data;
            // console.log(this.care);
            }
        );
        axios({
            url:'/api/makeup',
        }).then(
        res=>{
            this.makeup = res.data.data;
            // console.log(this.makeup);
            }
        );
        axios({
            url:'/api/perfume',
        }).then(
        res=>{
            this.perfume = res.data.data;
            // console.log(this.perfume);
            }
        );
        axios({
            url:'/api/tool',
        }).then(
        res=>{
            this.tool = res.data.data;
            // console.log(this.tool);
            }
        );
        axios({
            url:'/api/man',
        }).then(
        res=>{
            this.man = res.data.data;
            // console.log(this.man);
            }
        );
        axios({
            url:'/api/bath',
        }).then(
        res=>{
            this.bath = res.data.data;
            // console.log(this.bath);
            }
        );
        axios({
            url:'/api/hair',
        }).then(
        res=>{
            this.hair = res.data.data;
            // console.log(this.hair);
            }
        );
        axios({
            url:'/api/gift',
        }).then(
        res=>{
            this.gift = res.data.data;
            // console.log(this.gift);
            }
        );
    }
}
</script>

<style lang="scss">
@import url("//at.alicdn.com/t/font_1169325_cjl14h0twxd.css");
.follow .header{width: 100%;height: 0.8rem;text-align:center;line-height:0.8rem;background: rgb(7, 114, 25);display: flex;flex-direction: row;font-size: 0.3rem;color: #fff;}
.follow .header .l-float{margin: 0 1.6rem 0 0.1rem;}
.follow .header .l-float a{color: #fff;}
.follow{
    .tabs{display: flex;flex-direction: row;overflow-x: scroll;overflow-y: hidden;width: 100%;box-sizing: border-box;background: #fff;z-index: 12;
        .test{height:.5rem;line-height:.5rem;position: relative;white-space:nowrap; margin: 0.2rem;font-size: 0.3rem;font-family: PingFangSC-Regular;margin: 0 0.1rem;}
        .tab{color: orangered;}
    }
}
</style>
