<template>
    <div class="compaign">
        <div class="header">
            <div class="l-float">
                <em class="iconfont icon-arrow"></em>
                <a href="javascript:;" onclick="window.history.go(-1)">返回</a>
            </div>
            <div class="r-float">优惠专享</div>
        </div>
        <div class="compaign">
            <Imglist :imglist="imglist" :image="image" dataName="home" ></Imglist>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import { Header } from 'mint-ui';
Vue.component(Header.name, Header);

import Imglist from '@/components/Imglist';
export default {
    name:'compaign',
    data(){
        return {
            imglist:[],
            image:[]
        }
    },
    components:{Imglist},
    mounted(){
        axios({
            url:'/api/imglist',
        }).then(
        res=>{
            this.imglist = res.data.data;
            // console.log(this.imglist);
            }
        );

        axios({
            url:'/api/image',
        }).then(
        res=>{
            this.image = res.data.data;
            // console.log(this.image);
        }
        )
    }
        
}
</script>
<style lang="scss">
@import url("//at.alicdn.com/t/font_1169325_cjl14h0twxd.css");
.compaign .header{width: 100%;height: 0.8rem;text-align:center;line-height:0.8rem;background: rgb(7, 114, 25);display: flex;flex-direction: row;font-size: 0.3rem;color: #fff;}
.compaign .header .l-float{margin: 0 1.6rem 0 0.1rem;}
.compaign .header .l-float a{color: #fff;}
</style>

