<template>
  <div class="home">
    <Slider :banner="banner" class="top"></Slider>
    <Navigation :navigation="navigation" dataName="home" ></Navigation>
    <!-- 需要传递的数据，必须动态绑定 -->
    <Imglist :imglist="imglist" :image="image" dataName="home" ></Imglist>
  </div>
</template>
<script>
import Slider from '@/components/Slider'
import Navigation from '@/components/Navigation'
import Imglist from '@/components/Imglist'

export default {
  name:'home',
  data(){
    return {
      banner:[],
      navigation:[],
      imglist:[],
      image:[]
    }
  },
  components:{Slider,Navigation,Imglist},
  // 记录页面滚动距离3.离开页面时，记录页面的滚动距离
  beforeRouteLeave(to,from,next){
    let position = document.documentElement.scrollTop;
    localStorage.setItem('position-home',position);
    document.documentElement.removeEventListener('scroll',this.scrollTop);
    next();
  },
  // 记录页面滚动距离4.侦听路由跳转，设置滚动高度
  watch:{
    $route(newVal){
      if(newVal.path == '/home'){
        document.documentElement.scrollTop = localStorage.getItem('position-home');
      }
    }
  },
  mounted(){
    axios({
      url:'/api/home',
      params:{_page:1,_limit:10}
    }).then(
      res=>{
        this.list = res.data.data;
      }
    )
    axios({
      url:'/api/banner',
      params:{_page:1,_limit:3}
    }).then(
      res=>this.banner = res.data.data
    );
    axios({
      url:'/api/navigation',
    }).then(
      res=>this.navigation = res.data.data
    );
    axios({
      url:'/api/imglist',
    }).then(
      res=>{
          this.imglist = res.data.data;
          // console.log(this.imglist);
        }
    );
    axios({
      url:'/api/image',
    }).then(
      res=>{
        this.image = res.data.data;
        // console.log(this.image);
      }
    );
  }
}
</script>