import {
    ADD_ITEM,
    CHANGE_ITEM,
    REMOVE_ITEM,
    CLEAR_BUYCAR
} from './types'

let mutations = {
    increment(state, payload) {
        state.count += payload;
    },

    ['ADD_ITEM']: (state, payload) => {
        state.buycar = payload;
    },
    [CHANGE_ITEM]: (state, payload) => {
        //替换的是actions传递过来的新值
        state.buycar = payload;
    },
    [REMOVE_ITEM]: (state, payload) => {
        state.buycar = payload;
    },
    [CLEAR_BUYCAR]: (state, payload) => {
        state.buycar = payload;
    },
};

export default mutations;