<template>
  <div class="makeList">
    <ul>
        <router-link
            v-for="item of makeup"
            :key="item._id"
            tag="li"
            :to="{path:'/detail/'+item._id, query: {class: item.class}}"
        >
            <img :src="item.src" alt=""/>
            <span>{{item.title}}</span>
            <span>{{item.produce}}</span>
            <span>￥{{item.price}}.00
            <button @click.stop="addCar(item)">+</button>
            </span>
        </router-link>
    </ul>
  </div>
</template>
<script>
export default {
    props:['makeup'],
     methods:{
      addCar(item){
        axios({
          url:'/api/shopcar',
          method:'post',
          data:item
        }).then(
          res => {
            console.log(res.data);
          }
        )
      }
    }
}
</script>

<style>
.makeList{width:100%; margin:0 auto;}
.makeList ul{display: flex;flex-direction: row;flex-wrap: wrap;background: rgb(247, 239, 239);justify-content: space-around;}
.makeList ul li{display:inline-block;width:48%;color:#494d4d;text-align: center;background: #fff;margin: 0.05rem}
.makeList ul li img{width: 80%;}
.makeList ul li span{display: block;text-overflow: ellipsis;}
.makeList ul li span button{border-radius:0.1rem;border:0;}
</style>