<template>
  <div id="app">
    <Loading v-show="$root.$data.bLoading"></Loading>
    <Header v-show="$root.$data.bNav"></Header>
    <!-- 记录页面滚动距离2.能够将活动的组件实例保存在内存中，而不是直接将其销毁，是一个抽象组件，不会被渲染到真实DOM中，也不会出现在父组件链中 -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <Footer v-show="$root.$data.bFoot"></Footer>
  </div>
</template>

<script>

import Header from './components/Header'
import Footer from './components/Footer'
import Loading from './components/Loading'

export default {
  name: 'app',
  watch:{
    $route:{
      handler({path}){
        if(/home|column/.test(path)){
          this.$root.$data.bNav=true;
          this.$root.$data.bFoot=true;
        }
        if(/user|follow|compaign/.test(path)){
          this.$root.$data.bNav=false;
          this.$root.$data.bFoot=true;
        }
        if(/login|reg|detail|shopcar/.test(path)){
          this.$root.$data.bNav=false;
          this.$root.$data.bFoot=false;
        }
      },
      immediate:true
    }
  },
  components: {
    Loading,Header,Footer
  },
}
</script>

<style>
.box{background:red}
</style>
